import { graphql, useStaticQuery } from "gatsby"
import React from "react"
import CarouselSlide from "../components/carousel"


const MattlaggningCarousel = () => {
    const { gcms: { mattlaggnings } } = useStaticQuery(graphql`
    {
        gcms {
            mattlaggnings {
                bildspel {
                  url
                }
                desc
                title
              }
        }
    }
  `)


    return (

        <CarouselSlide images={mattlaggnings.map(x=>{return {url:x.bildspel.url, desc:x.desc, title:x.title}})} />

    )
}
export default MattlaggningCarousel;